/*! HEADER */

header {
    padding: 1.25rem 0;
    position: fixed;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    top: 0;
    left: 0;
    z-index: 9999;
    background-color: rgba(0, 0, 0, 0.7);
    backdrop-filter: blur(8px);
    &.night-mode {
        background-color: rgb(0, 0, 0, );
    }
    .toggle-night-mode {
        display: flex;
        justify-content: center;
        align-items: center;
        line-height: 1;
        #slider {
            font-size: 15px;
            appearance: none;
            width: 60px;
            height: 30px;
            background-color: #e5e5e5;
            border-radius: 3em;
            position: relative;
            cursor: pointer;
            outline: none;
            transition: all .2s ease-in-out;
            border: 2px solid #fff;
            &:after {
                position: absolute;
                content: "";
                width: 30px;
                height: 30px;
                border-radius: 50%;
                background-color: #222;
                box-shadow: 0 0 0.25em rgba(0, 0, 0, 0.3);
                transform: scale(.7);
                top: -2px;
                left: 0;
                transition: all .2s ease-in-out;
            }
            &:checked {
                width: 60px;
                height: 30px;
                background-color: rgb(20, 20, 20);
                transition: all .2s ease-in-out;
            }
        }
    }
    h1 {
        font-family: "SUSE", sans-serif;
        display: flex;
        align-items: center;
        font-size: 2.2rem;
        letter-spacing: 3px;
        word-spacing: 5px;
        color: #fff;
        text-transform: uppercase;
        &.night-mode {
            color: #fff;
        }
    }
    .header-button {
        display: flex;
        justify-content: center;
        align-items: center;
        color: #000;
        font-size: 1.05rem;
        font-weight: 600;
        word-spacing: 1.75px;
        letter-spacing: 0.1px;
        line-height: calc(1.05rem * 1.22);
        border-radius: 8px;
        padding: 0.7rem 1.25rem;
        outline: none;
        border: none;
        transition: .18s ease-in-out;
        cursor: pointer;
        user-select: none;
        a {
            font-family: "Lexend", sans-serif;
            color: #000;
            text-decoration: none;
            transition: .18s ease-in-out;
        }
        &:hover {
            background-color: #3C096C;
            a {
                color: #fff;
            }
        }
    }
}

.toggle-night-mode #slider:checked:after {
    left: calc(100% - 2.075em);
    background-color: #e5e5e5;
}

@media screen and (max-width: 1023px) {
    header {
        h1 {
            font-size: 2rem;
            letter-spacing: 2px;
        }
        .header-button {
            padding: 0.525rem 1.1rem;
            font-size: 1rem;
        }
    }
}

@media screen and (max-width: 767px) {
    header {
        padding: 1.75rem 0 1.5rem;
        h1 {
            font-size: 1.7rem;
            letter-spacing: 1px;
            word-spacing: 2px;
        }
        .header-button {
            padding: 0.525rem 1.1rem;
            font-size: 1rem;
        }
        .toggle-night-mode {
            #slider {
                width: 50px;
                height: 25px;
                &:after {
                    width: 25px;
                    height: 25px;
                }
                &:checked {
                    width: 50px;
                    height: 25px;
                }
            }
        }
    }
    .toggle-night-mode #slider:checked:after {
        left: calc(100% - 1.85em);
        background-color: #e5e5e5;
    }
}

@media screen and (max-width: 565px) {
    header {
        h1 {
            font-size: 1.5rem;
            letter-spacing: 0.25px;
            word-spacing: 0px;
        }
        .header-button {
            padding: 0.5rem 1.05rem;
            font-size: 0.875rem;
        }
    }
    .toggle-night-mode #slider:checked:after {
        left: calc(100% - 1.725em);
        background-color: #e5e5e5;
    }
}

@media screen and (max-width: 450px) {
    header {
        h1 {
            font-size: 1.125rem;
            letter-spacing: 0px;
        }
        .header-button {
            padding: 0.3rem 0.6rem;
            font-size: 0.75rem;
        }
        .toggle-night-mode {
            #slider {
                width: 42.5px;
                height: 21px;
                &:after {
                    width: 21px;
                    height: 21px;
                }
                &:checked {
                    width: 42.5px;
                    height: 21px;
                }
            }
        }
    }
    .toggle-night-mode #slider:checked:after {
        left: calc(100% - 1.5em);
        background-color: #e5e5e5;
    }
}