/*! DATA CHARTS */

.data_charts {
    color: #000;
    margin-top: 15vh;
    h2 {
        font-size: 2.8rem;
        line-height: calc(2.7rem * 1.5);
    }
    .data_charts-info {
        font-size: 1.25rem;
        line-height: calc(1.25rem * 1.25);
        margin-top: 1.25rem;
        user-select: none;
        &.dark__mode {
            color: #d4d4d4 !important;
        }
    }
    .products_stats {
        display: flex;
        width: 70%;
        justify-content: space-between;
        align-items: center;
        margin: 3rem auto 7.5rem;
        .single-info-wrapper {
            padding: 1.5rem;
            text-align: left;
            width: 30%;
            border-radius: 6px;
            border: 1px solid rgba(0, 0, 0, 0.3);
            p {
                font-size: 1.3rem;
                font-weight: 500;
                color: rgba(0, 0, 0, 0.55);
                display: flex;
            }
            span {
                font-size: 2.3rem;
                font-weight: 700;
                display: flex;
                margin: 0.75rem 0 0;
                &.dark__mode {
                    color: #80c423 !important;
                }
            }
            &.dark__mode {
                border-color: #3a0ca3;
                border-width: 2px;
            }
        }
    }
    .radial_chart-pie {
        width: 80%;
        margin: 0 auto;
    }
}

#productCategories {
    h2 {
        margin-top: 10vh;
        font-size: 2.7rem;
    }
}

@media screen and (max-width: 767px) {
    .data_charts {
        margin-top: 7.5vh;
        h2 {
            font-size: 2.6rem;
            line-height: 4rem;
        }
        .data_charts-info {
            font-size: 1.05rem;
            width: 90%;
            margin-left: auto;
            margin-right: auto;
            line-height: 1.5rem;
        }
        .products_stats {
            width: 97.5%;
            margin-bottom: 2.5rem;
            .single-info-wrapper {
                width: 31%;
                padding: 0.5rem 0.25rem;
                p {
                    width: 90%;
                    margin: auto;
                    font-size: 1rem;
                }
                span {
                    font-size: 1.5rem;
                    justify-content: center;
                }
            }
        }
        .radial_chart-pie {
            width: 90%;
        }
    }
    #productCategories h2 {
        font-size: 2.2rem;
        margin-top: 8vh;
    }
}