/*! FOOTER */

footer {
    display: flex;
    width: 50%;
    padding-top: 2.5vh;
    margin: 7.5vh auto 2.75vh;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    p {
        color: #000;
        font-weight: 500;
        font-size: 1.25rem;
        padding: 0 1rem;
        letter-spacing: -0.3px;
        cursor: pointer;
    }
}

@media screen and (max-width: 1450px) {
    footer {
        justify-content: space-between;
        width: 57.5%;
        p {
            font-size: 1.15rem;
        }
    }
}

@media screen and (max-width: 1150px) {
    footer {
        margin-bottom: 2.25vh;
        width: 65%;
        p {
            font-size: 1.1rem;
        }
    }
}

@media screen and (max-width: 950px) {
    footer {
        width: 75%;
        p {
            font-size: 1.075rem;
        }
    }
}

@media screen and (max-width: 820px) {
    footer {
        p {
            font-size: 1rem;
        }
    }
}

@media screen and (max-width: 767px) {
    footer {
        width: 80%;
        p {
            font-size: 0.825rem;
            padding: 0 0.25rem;
        }
    }
}

@media screen and (max-width: 620px) {
    footer {
        justify-content: space-around;
        width: 100%;
    }
}