/* ! ADD PRODUCT */

.add_product {
    margin: 0 auto;
    width: 82.5%;
    height: 100vh;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    user-select: none;
    position: relative;
    .added-modal {
        // SMALL NOTIFICATION IN THE BOTTOM LEFT CORNER
        z-index: 10001;
        font-family: 'Roboto';
        line-height: calc(1.125rem * 1.3);
        font-size: 1.05rem;
        letter-spacing: 0.15px;
        word-spacing: 2px;
        position: fixed;
        right: 2.75rem;
        bottom: 2.5rem;
        background-color: rgb(235, 235, 235);
        color: #000;
        border-radius: 12px;
        padding: 0.825rem 1.8rem;
        border: 1px solid black;
        display: none;
        justify-content: center;
        align-items: center;
        opacity: 0;
        transition: .25s ease-in-out;
        .icon {
            background-color: black;
            color: #fff;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 1.15rem;
            margin-right: 0.4rem;
        }
        &.modal-active {
            opacity: 1;
            display: flex;
        }
    }
    .add_product-container {
        text-align: left;
        width: 50%;
        h2 {
            color: #000;
            font-size: 3.2rem;
            font-weight: 700;
            text-align: left;
            letter-spacing: -0.75px;
            word-spacing: 4px;
            margin-bottom: 3rem;
            line-height: calc(3rem * 1.5);
            &.dark__mode {
                color: #fff;
            }
        }
        label {
            color: #000;
            font-weight: 500;
            font-size: 1.3rem;
            line-height: calc(1.3rem * 1.3);
            input {
                display: block;
                margin-bottom: 1.5rem;
                margin-top: 0.25rem;
                width: 60%;
                text-align: left;
                padding: 9px 12px;
                font-size: 1rem;
                border-radius: 6px;
                border: 1px solid #707070;
            }
        }
        .products_categories {
            margin-top: 0.1rem;
            display: flex;
            flex-direction: row;
            justify-content: flex-start;
            max-width: 90%;
            flex-wrap: wrap;
            .product-category-selector {
                font-size: 1.125rem;
                font-weight: 400;
                line-height: calc(1.125rem * 1.25);
                background: rgba(0, 0, 0, 0.125);
                padding: 8px 10px;
                margin-right: 6px;
                margin-top: 6px;
                border-radius: 6px;
                border: none;
                outline: none;
                position: relative;
                cursor: pointer;
                transition: 0.15s cubic-bezier(0.4, 0, 0.2, 1);
                &.dark__mode {
                    font-weight: 500;
                    color: #000 !important;
                    background-color: beige;
                }
                .span_name {
                    position: relative;
                    z-index: 10;
                    transition: 0.15s cubic-bezier(0.4, 0, 0.2, 1);
                    &.selected_item {
                        color: white;
                    }
                }
                .animated_tile {
                    position: absolute;
                    inset: 0;
                    z-index: 0;
                    border-radius: 6px;
                    //background-image: linear-gradient(90deg, #7c3aed, #4f46e5);
                    background-color: #000;
                    &.dark__mode {
                        background-color: rgb(0, 13, 255);
                    }
                }
            }
        }
        .form__buttons {
            margin-top: 1rem;
            display: flex;
            flex-direction: row;
            .form__cancel,
            .add {
                line-height: 24px;
                display: flex;
                justify-content: center;
                align-items: center;
                border-radius: 8px;
                padding: 12px;
                width: 160px;
                height: 48px;
                outline: none;
                border: 1px solid #000;
                font-size: 1rem;
                font-weight: 600;
                cursor: pointer;
            }
            .form__cancel {
                background-color: #fff;
                &.dark__mode {
                    color: #000 !important;
                }
            }
            .add {
                background-color: #000;
                color: #fff;
                margin-left: 12px;
                &.dark__mode {
                    background-color: #3C096C;
                    border-color: transparent;
                }
            }
        }
    }
}

.input-container {
    position: relative;
    margin: 5rem 0 2rem;
    width: 60%;
    input[type="text"] {
        font-size: 20px;
        line-height: calc(20px * 1.25);
        width: 100%;
        border: none;
        border-bottom: 2px solid #ccc;
        padding: 5px;
        background-color: transparent;
        outline: none;
        text-align: left;
        &.dark__mode {
            color: #fff;
        }
    }
    .label {
        position: absolute;
        top: 0;
        left: 0;
        color: #000;
        transition: all 0.3s ease;
        pointer-events: none;
    }
    .underline {
        position: absolute;
        bottom: 0;
        left: 0;
        height: 2px;
        width: 100%;
        background-color: #333;
        transform: scaleX(0);
        transition: all 0.3s ease;
        &.dark__mode {
            background-color: #666;
        }
    }
}

.input-container input[type="text"]:focus~.label,
.input-container input[type="text"]:valid~.label {
    top: -20px;
    font-size: 16px;
    color: #333;
}

.input-container input[type="text"]:focus~.underline,
.input-container input[type="text"]:valid~.underline {
    transform: scaleX(1);
}

.add_product-image {
    object-fit: contain;
    object-position: center;
    width: 55%;
    height: auto;
    margin-top: 10%;
    user-select: none;
}

@media screen and (max-width: 1450px) {
    .add_product-image {
        width: 57.5%;
        margin-top: 7.5%;
        margin-left: 5%;
    }
}

@media screen and (max-width: 1100px) {
    .add_product {
        .add_product-container {
            width: 52.5%;
            h2 {
                width: 120%;
                margin-bottom: 1.5rem;
            }
        }
        .added-modal {
            right: 2rem;
            bottom: 2rem;
            font-size: 1rem;
            letter-spacing: 0.1px;
            padding: 0.65rem 1.35rem;
        }
    }
    .add_product-image {
        width: 65%;
        margin-top: 5%;
        margin-left: 17.5%;
    }
}

@media screen and (max-width: 860px) {
    .add_product {
        width: 85%;
        .add_product-container {
            h2 {
                font-size: 2.6rem;
            }
        }
        .added-modal {
            right: 1.75rem;
            bottom: 1.75rem;
            font-size: 0.95rem;
            letter-spacing: 0.075px;
            padding: 0.55rem 1.25rem;
            .icon {
                font-size: 1.075rem;
                margin-right: 0.35rem;
            }
        }
    }
    .input-container {
        margin: 3.25rem 0 2rem;
    }
    .add_product-image {
        width: 70%;
        margin-top: 7.5%;
        margin-left: 22.5%;
    }
}

@media screen and (max-width: 767px) {
    .add_product {
        width: 100%;
        flex-direction: column;
        .add_product-container {
            width: 90%;
            h2 {
                font-size: 2.3rem;
                width: auto;
                text-align: center;
            }
            label {
                display: flex;
                flex-direction: column;
                align-items: center;
            }
            .products_categories {
                justify-content: center;
                .product-category-selector {
                    font-size: 0.95rem;
                    padding: 7px 9px;
                    margin-right: 5px;
                    margin-top: 5px;
                }
            }
            .form__buttons {
                margin-top: 4vh;
                justify-content: space-evenly;
                .form__cancel {
                    color: #000;
                }
                .form__cancel,
                .add {
                    max-width: 42.5%;
                    height: 2.85rem;
                    width: 40%;
                    padding: 10px;
                }
            }
        }
    }
    .input-container {
        margin: 3rem auto 2rem;
        label {
            right: 0;
        }
    }
    .add_product-image {
        width: 82.5%;
        margin-top: 7vh;
        margin-left: 0;
    }
}