@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Maven+Pro:wght@400..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Chakra+Petch:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,500;1,600;1,700&family=Lexend:wght@100..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=SUSE:wght@100..800&display=swap');

* {
    font-family: "Maven Pro", sans-serif;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    text-align: center;
    /*color: #001C30; */
    scrollbar-width: thin;
}

body {
    color: #001C30;
    scrollbar-width: thin;
}

body.night-mode {
    background-color: rgb(25, 25, 25);
    background-color: #03001C;
    color: #fff;
}

ul {
    list-style: none;
}

img {
    user-select: none
}

.dark__mode {
    color: beige !important;
}

.mobile-br {
    display: none;
}

.items__list {
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    padding-top: 12.5vh;
    padding-bottom: 0.25rem;
    border-bottom: 2px solid #001C30;
    font-weight: 600;
}


/* SELECT VIEW */

.select-view-options {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    width: auto;
    margin: 0 4rem 3% auto;
}

.select-view-options .select-option {
    font-size: 1.55rem;
    cursor: pointer;
}

.select-view-options .select-option:first-of-type {
    margin-right: 1rem;
    font-size: 1.9rem;
}

.item__remove {
    /*order: 4; */
    order: -1;
    margin-left: auto;
    margin-right: 10%;
}


/*! MEDIA QUERIES */

@media screen and (max-width:767px) {
    .remove__item {
        width: 30px;
        min-height: 30px;
        line-height: normal;
        padding: 0;
    }

    .mobile-br {
        display: flex;
    }
}