.products {
    background-color: rgb(238, 239, 255);
    padding: 10vh 0;
    margin: 5vh 0;
    &.dark__mode {
        background-color: #00111c;
    }
    .products__list-container {
        margin: auto;
        width: 90%;
        display: flex;
        align-items: center;
        background-color: rgb(255, 255, 255);
        color: #000;
        border-radius: 14px;
        flex-wrap: wrap;
        &.dark__mode {
            background-color: transparent;
        }
        .products__nav {
            color: #001C30;
            line-height: calc(1.5rem * 1.4);
            font-size: 1.5rem;
            text-transform: uppercase;
            font-weight: 800;
            padding: 1.25rem 0.25rem;
            width: 100%;
            justify-content: space-between;
            display: flex;
            flex-direction: row;
            text-align: left;
            background-color: #c0c0f9;
            border-top-left-radius: 14px 14px;
            border-top-right-radius: 14px 14px;
            &.dark__mode {
                border-top-left-radius: 18px 18px;
                border-top-right-radius: 18px 18px;
                background-color: #00253e;
                background-color: #001a2c;
            }
            .products_category {
                &:nth-of-type(1) {
                    width: 20%;
                }
                &:nth-of-type(2) {
                    width: 30%;
                }
                &:nth-of-type(3) {
                    width: 37.5%;
                }
                &:last-of-type {
                    width: 12.5%;
                }
            }
            .expiration-date {
                display: flex;
                align-items: center;
                justify-content: center;
                cursor: pointer;
                .arrow-icon {
                    margin-top: 0.21rem;
                    margin-left: 0.125rem;
                }
            }
        }
        .product__row {
            font-weight: 500;
            line-height: calc(1.15rem * 1.25);
            font-size: 1.15rem;
            padding: 1rem 0.25rem;
            width: 100%;
            justify-content: space-between;
            display: flex;
            align-items: center;
            flex-direction: row;
            text-align: left;
            border-top: 1px solid rgb(190, 190, 190);
            &:nth-of-type(2n + 1) {
                background-color: rgb(240, 240, 240);
                &.dark__mode {
                    background-color: #012c48;
                }
            }
            &.dark__mode {
                background-color: #00253e;
                color: beige;
                &:last-of-type {
                    border-bottom-left-radius: 18px 18px;
                    border-bottom-right-radius: 18px 18px;
                }
            }
            li {
                &:nth-of-type(1) {
                    width: 20%;
                }
                &:nth-of-type(2) {
                    width: 30%;
                }
                &:nth-of-type(3) {
                    width: 37.5%;
                }
                &:last-of-type {
                    width: 12.5%;
                    display: flex;
                    justify-content: center;
                }
            }
            .proudctName-row {
                width: 20%;
                text-transform: capitalize;
                overflow: hidden;
                padding: 0 3rem;
            }
            .category-row {
                margin: 0 auto;
                width: 30%;
                display: flex;
                align-items: center;
                justify-content: space-around;
                .cathegory-thumbnail {
                    height: 35px;
                    width: 35px;
                    user-select: none;
                }
            }
            .expirationDate-row {
                width: 35%;
                letter-spacing: -0.25px;
                word-spacing: -1px;
                font-size: 1.05rem;
            }
        }
    }
}

.expired {
    color: #ae0303;
    text-transform: uppercase;
    font-weight: 600;
}

.expiring-today {
    color: #DD5746;
    text-transform: uppercase;
    font-weight: 600;
}

.button {
    width: 45px;
    height: 45px;
    border-radius: 50%;
    background-color: rgb(20, 20, 20);
    border: none;
    font-weight: 600;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.164);
    cursor: pointer;
    transition-duration: .3s;
    overflow: hidden;
    position: relative;
    &:hover {
        width: 50%;
        border-radius: 50px;
        transition-duration: .3s;
        background-color: #ae0303;
        align-items: center;
    }
    &::before {
        position: absolute;
        top: -22.5px;
        content: "Delete";
        color: white;
        transition-duration: .3s;
        font-size: 2px;
    }
}

.svgIcon {
    width: 12px;
    transition-duration: .3s;
}

.svgIcon path {
    fill: white;
}

.button:hover .svgIcon {
    width: 50px;
    transition-duration: .3s;
    transform: translateY(57.5%);
}

.button:hover::before {
    font-size: 13px;
    opacity: 1;
    transform: translateY(30px);
    transition-duration: .3s;
}

@media screen and (max-width: 1450px) {
    .products {
        .products__list-container {
            .product__row {
                .category-row {
                    width: 35%;
                    //justify-content: space-between;
                }
            }
        }
    }
    .button {
        &:hover {
            width: 60%;
            &::before {
                transform: translateY(28.5px);
            }
        }
    }
}

@media screen and (max-width: 1280px) {
    .button {
        &:hover {
            width: 70%;
        }
    }
}

@media screen and (max-width: 1100px) {
    .products {
        .products__list-container {
            width: 95%;
            .products__nav {
                font-size: 1.4rem;
                .products_category {
                    &:nth-of-type(1) {
                        width: 17.5%;
                    }
                    &:nth-of-type(2) {
                        width: 30%;
                    }
                    &:nth-of-type(3) {
                        width: 37.5%;
                    }
                    &:last-of-type {
                        width: 15%;
                    }
                }
            }
            .product__row {
                .proudctName-row {
                    padding: 0 1.75rem;
                }
                .category-row {
                    width: 37.5%;
                    justify-content: space-between;
                }
                li {
                    &:nth-of-type(1) {
                        width: 17.5%;
                    }
                    &:nth-of-type(2) {
                        width: 30%;
                    }
                    &:nth-of-type(3) {
                        width: 37.5%;
                    }
                    &:last-of-type {
                        width: 15%;
                        display: flex;
                        justify-content: center;
                    }
                }
            }
        }
    }
    .button {
        &:hover {
            width: 73.5%;
        }
    }
}

@media screen and (max-width: 960px) {
    .products {
        .products__list-container {
            width: 97.5%;
            .product__row {
                .proudctName-row {
                    padding: 0 1rem;
                }
                .category-row {
                    width: 42.5%;
                }
            }
        }
    }
    .button {
        &:hover {
            width: 77%;
        }
    }
}

@media screen and (max-width: 767px) {
    .products {
        padding-bottom: 7vh;
        .products__list-container {
            .products__nav {
                padding: 1rem 0.25rem 1rem 0rem;
                font-size: 1.2rem;
            }
            .product__row {
                padding: 0.825rem 0.25rem;
                font-size: 1.05rem;
                .expirationDate-row {
                    width: 35%;
                    letter-spacing: -0.35px;
                    word-spacing: -1.75px;
                    font-size: 0.975rem;
                }
            }
        }
    }
    .button {
        &:hover {
            width: 87.5%;
        }
    }
}

@media screen and (max-width: 630px) {
    .products {
        .products__list-container {
            .products__nav {
                padding-top: 0.5rem;
                padding-bottom: 0.45rem;
                font-size: 0.825rem;
                font-weight: 600;
                letter-spacing: -0.2px;
                word-spacing: -0.25px;
                .products_category {
                    &:nth-of-type(1) {
                        width: 17.5%;
                    }
                    &:nth-of-type(2) {
                        width: 27.5%;
                    }
                    &:nth-of-type(3) {
                        width: 41%;
                    }
                    &:last-of-type {
                        width: 14%;
                    }
                }
                .expiration-date {
                    .arrow-icon {
                        margin-top: 0.15rem;
                        margin-left: 0.05rem;
                    }
                }
            }
            .product__row {
                font-size: 1rem;
                li {
                    &:nth-of-type(1) {
                        width: 17.5%;
                    }
                    &:nth-of-type(2) {
                        width: 27.5%;
                    }
                    &:nth-of-type(3) {
                        width: 41%;
                    }
                    &:last-of-type {
                        width: 14%;
                    }
                }
                .category-row {
                    width: auto;
                    justify-content: center;
                    .cathegory-thumbnail {
                        height: 25px;
                        width: 25px;
                    }
                }
                .expirationDate-row {
                    font-size: 0.9rem;
                    line-height: 1.15rem;
                }
            }
        }
    }
    .button {
        height: 35px;
        width: 35px;
        &:hover {
            width: 95%;
            &::before {
                transform: translateY(26px)
            }
        }
    }
}

@media screen and (max-width: 600px) {
    .products .products__list-container .product__row:last-of-type {
        align-self: center;
        flex-grow: 1;
    }
    .products .products__list-container .product__row.dark__mode:last-of-type {
        border-radius: 0;
    }
    .products .products__list-container .product__row:nth-of-type(2n + 1) {
        background-color: #dcdcfa;
        &.dark__mode {
            background-color: #00253e;
        }
    }
    .products {
        .products__list-container {
            width: 96.5%;
            justify-content: space-between;
            background-color: transparent;
            .products__nav {
                display: none;
            }
            .product__row {
                background-color: #dcdcfa;
                margin-top: 1rem;
                flex-direction: column;
                flex-wrap: wrap;
                height: auto;
                width: 48%;
                border: none;
                padding: 1rem 0.25rem;
                li {
                    &:nth-of-type(1) {
                        font-size: 1.05rem;
                        font-weight: 600;
                        width: auto;
                    }
                    &:nth-of-type(2) {
                        width: auto;
                    }
                    &:nth-of-type(3) {
                        width: auto;
                    }
                    &:last-of-type {
                        width: 100%;
                    }
                }
                .category-row {
                    margin-top: 0.6rem;
                    font-size: 1.1rem;
                    font-weight: 400;
                    display: flex;
                    flex-direction: row-reverse;
                    align-items: center;
                    justify-content: center;
                    .cathegory-thumbnail {
                        margin-right: 0.35rem;
                        height: 23px;
                        width: 23px;
                    }
                }
                .expirationDate-row {
                    margin-top: 0.75rem;
                    font-size: 1.05rem;
                }
            }
        }
    }
    .button {
        margin-top: 0.55rem;
        height: 38px;
        width: 38px;
        &:hover {
            width: 45%;
        }
    }
}